import { nanoid } from 'nanoid';

export const ProductsList = ({ list }) => {
  const items = list.map(({ count, price, type }) => {
    const id = nanoid();
    return (
      <li className="poultry__item" key={id}>
        <span>{type}</span>
        <span>{`${count}`}</span>
        <span>{`${price}`}</span>
      </li>
    );
  });

  return <ul className="poultry__list">{items}</ul>;
};
